const readFile = async (file: string): Promise<Map<string, Array<string>>> => {
  console.log(`Reading file ${file}...`);

  return fetch(file)
    .then((response) => response.text())
    .then((data) => {
      let result = new Map<string, Array<string>>();

      let lines = data.split(/\n/);
      let terms = new Array<string>();

      lines.forEach((line, index) => {
        if (line.startsWith("<B>")) {
          let term = line.substring(3, line.indexOf("</B>"));
          terms.push(term);
          var defs = result.get(term);
          if (defs === undefined) {
            defs = new Array<string>();
            result.set(term, defs);
          }
          defs.push(line.replace("<B>", "<B class='term'>"));
        }
      });

      // let sortedTerms = [...terms].map((item) => normalize(item)).sort();
      // for (let index = 0; index < terms.length; index++) {
      //   let original = terms[index];
      //   let sorted = sortedTerms[index];
      //   if (normalize(original) !== sorted) {
      //     console.error("Error in file " + file + ", line " + index, original, sorted);
      //     break;
      //   }
      // }

      console.log(`Loaded ${lines.length} definitions from ${file}`, terms);

      return result;
    });
};

export const readFiles = async (): Promise<Map<string, Array<string>>> => {
  let fileParts = ["abc", "def", "g_i", "h", "jkl", "mno", "pqr", "stu", "vwxyz"];
  let fileResults: Array<Map<string, Array<string>>> = await Promise.all(
    fileParts.map((part) => {
      return readFile("/db/" + part + ".html");
    })
  );
  let definitions = new Map<string, Array<string>>(fileResults.flatMap((e) => [...e]));
  console.log(`Definition count: ${definitions.size}`);
  return definitions;
};

///

const normalize = (input: string): string => {
  return input
    .replaceAll("æ", "ae")
    .replaceAll("œ", "oe")
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace("*", "")
    .replace(",", "")
    .replace("'", "")
    .toLowerCase();
};

export const search = (
  query: string,
  searchInside: boolean,
  definitions: Map<string, Array<string>>
): Array<string> => {
  let output = new Array<string>();

  let normalizedQuery = normalize(query);

  definitions.forEach((defs, term) => {
    var found = false;
    if (searchInside) {
      for (var i = 0; i < defs.length; i++) {
        let def = defs[i];
        let normalizedDef = normalize(def);
        if (normalizedDef.includes(normalizedQuery)) {
          found = true;
          break;
        }
      }
    } else {
      let normalizedTerm = normalize(term);
      found = normalizedTerm.startsWith(normalizedQuery);
    }
    if (found) {
      output.push(...defs);
    }
  });

  return output.sort((a: string, b: string) =>
    a.replace("*", "").localeCompare(b.replace("*", ""))
  );
};
