import React, { useEffect, useRef, useState } from "react";
import "./App.css";
import {
  Group,
  TextInput,
  Text,
  Switch,
  Stack,
  ActionIcon,
  Container,
  Table,
  ScrollArea,
} from "@mantine/core";
import {
  IconChevronLeftPipe,
  IconChevronRightPipe,
  IconSearch,
  IconTrash,
} from "@tabler/icons-react";
import { readFiles, search } from "./logic";

function App() {
  const [definitions, setDefinitions] = useState<Map<string, Array<string>>>(new Map());
  const [results, setResults] = useState<Array<string>>([]);

  const [historyVisible, setHistoryVisible] = useState(true);
  const [history, setHistory] = useState<Array<string>>([]);

  const [query, setQuery] = useState("");
  const [historyQuery, setHistoryQuery] = useState("");
  const [searchInside, setSearchInside] = useState(false);

  const resultsRef = useRef(null);
  const queryRef = useRef<HTMLInputElement>(null);

  ///

  useEffect(() => {
    readFiles().then((definitions) => setDefinitions(definitions));
  }, []);

  useEffect(() => {
    if (query.length >= 2) {
      setResults(search(query, searchInside, definitions));
      if (historyQuery !== query) {
        let updatedHistory = history.filter((item) => item !== query);
        updatedHistory.unshift(query);
        setHistory(updatedHistory);
      }
    } else {
      setResults([]);
    }

    // @ts-ignore
    // if (CSS && CSS.highlights) {
    //   // @ts-ignore
    //   CSS.highlights.clear();
    //   if (resultsRef.current && searchInside) {
    //     let treeWalker = document.createTreeWalker(resultsRef.current, NodeFilter.SHOW_TEXT);
    //     let textNodes = [];
    //     let currentNode = treeWalker.nextNode();
    //     while (currentNode) {
    //       textNodes.push(currentNode);
    //       currentNode = treeWalker.nextNode();
    //     }

    //     let ranges = new Array<Range>();
    //     textNodes.forEach((node) => {
    //       let text = node.textContent!.toLowerCase();
    //       let startPos = 0;
    //       while (startPos < text.length) {
    //         const index = text.indexOf(query, startPos);
    //         if (index === -1) {
    //           break;
    //         }

    //         const range = new Range();
    //         range.setStart(node, index);
    //         range.setEnd(node, index + query.length);
    //         if (!range.collapsed) {
    //           ranges.push(range);
    //         }

    //         startPos = index + query.length;
    //       }
    //     });

    //     console.log("Ranges", ranges);

    //     // @ts-ignore
    //     const searchResultsHighlight = new Highlight(...ranges);

    //     // @ts-ignore
    //     CSS.highlights.set("search-results", searchResultsHighlight);
    //   }
    // }
  }, [definitions, query, searchInside]);

  useEffect(() => {
    if (historyQuery.length > 0) {
      if (queryRef.current) {
        queryRef.current.value = historyQuery;
      }
      setQuery(historyQuery);
    }
  }, [historyQuery]);

  const resultsRows = results.map((entry, index) => (
    <Table.Tr key={entry}>
      <Table.Td>
        <div key={"r" + index} dangerouslySetInnerHTML={{ __html: entry }} />
      </Table.Td>
    </Table.Tr>
  ));

  const historyRows = history.map((entry) => (
    <Table.Tr key={entry}>
      <Table.Td
        className={entry === query ? "current" : ""}
        onClick={() => {
          setHistoryQuery(entry);
        }}
      >
        {entry}
      </Table.Td>
    </Table.Tr>
  ));

  return (
    <Group wrap="nowrap" align="flex-start" className="fr-dict" gap={0}>
      <Stack className="main" gap={0}>
        <Group className="header">
          <TextInput
            ref={queryRef}
            autoComplete="off"
            autoCapitalize="off"
            autoCorrect="off"
            spellCheck="false"
            size="lg"
            fw="bold"
            placeholder="Search"
            leftSection={<IconSearch size={20} />}
            leftSectionPointerEvents="none"
            rightSection={
              <Text size="sm" c="gray">
                {results.length}
              </Text>
            }
            rightSectionPointerEvents="none"
            error={query.length === 1}
            defaultValue={query}
            onKeyUp={(event) => {
              if (event.key === "Enter") {
                event.currentTarget.select();
                setQuery(event.currentTarget.value);
                setHistoryQuery("");
              }
            }}
          />
          <Switch
            label="Look inside definitions"
            checked={searchInside}
            onChange={(event) => setSearchInside(event.currentTarget.checked)}
          />
          <Container />
          {!historyVisible && (
            <ActionIcon
              variant="light"
              aria-label="Show History"
              title="Show History"
              onClick={() => {
                setHistoryVisible(!historyVisible);
              }}
            >
              <IconChevronLeftPipe size={20} />
            </ActionIcon>
          )}
        </Group>
        <ScrollArea>
          <Table className="resultsTable" ref={resultsRef}>
            <Table.Thead></Table.Thead>
            <Table.Tbody>{resultsRows}</Table.Tbody>
          </Table>
        </ScrollArea>
      </Stack>
      {historyVisible && (
        <Stack className="history" gap={0}>
          <Group className="header" wrap="nowrap">
            <Text fw="bold" c="blue">
              History
            </Text>
            <ActionIcon
              variant="light"
              aria-label="Clear History"
              title="Clear History"
              onClick={() => {
                setHistory([]);
              }}
            >
              <IconTrash size={16} />
            </ActionIcon>
            <Container />
            <ActionIcon
              variant="light"
              aria-label="Hide History"
              title="Hide History"
              onClick={() => {
                setHistoryVisible(!historyVisible);
              }}
            >
              <IconChevronRightPipe size={20} />
            </ActionIcon>
          </Group>
          <ScrollArea>
            <Table className="historyTable">
              <Table.Thead></Table.Thead>
              <Table.Tbody>{historyRows}</Table.Tbody>
            </Table>
          </ScrollArea>
        </Stack>
      )}
    </Group>
  );
}

export default App;
